/* The C Shore Generic Styling Elements for the menubar */

/* Remember, for the level of specificity last rule wins, hence
 *   most important rules are last
*/
@import "custom-properties.css";

/* Without Flex get the Topbar's Menubar to the right */
.Header {
  & .SitebarSitemap {
    text-align: left;
    vertical-align: top;
    display: inline-block;
    position: relative;
    top: 0;
    right: 0;
    z-index: 20;
  
    & .SitemapList {
      position: relative;
      top: 0;
      right: 0;
      z-index: 20;
      list-style: none;
      display: inline-block;
      margin: 0;
      padding: 0;

      & .MenuItem {
        display: inline-block;
        margin-top: .2em;
        margin-bottom: .2em;
        margin-left: .4em;
        margin-right: .4em;
        & a {
          text-decoration: none;
        }
        & .MenuEntryName {
          padding-left: .2em;
        }
      }

      & .MenuSubmenu {
        list-style: none;
        display: inline-block;
        margin: 0;
        padding: 0;
        width: 200%;

        & .MenuItem {
          width: 100%;
          padding: 0;
          margin: 0;
          border: none;
          & a {
            text-decoration: none;
            width: 100%;
            display: inline-block;
            & .MenuEntryName {
              padding: 0;
              margin: 0;
              width: 100%;
              border: none;
            }
          }
        }
      }
    }

    & .SitemapList,
    & .MenuSubmenu {
      list-style: none outside;

      & .MenuItem {
        display: inline-block;

        & .MenuEntryName {
          display: inline-block;
          width: 100%;
        }
      }

      & .MenuParent {
        position: relative;
        z-index: 20;
        top: 0;
        right: 0;

        & .MenuSubmenu {
          top: 100%;
          right: 4px;
          position: absolute;
          z-index: 22;
          border: 4px solid;
          display: none;
          & .MenuItem {
            border: none;
            & a {
              border: none;
            }
          }
          & .MenuParent {
            position: relative;
            z-index: 22;
            top: 0;
            right: 0;

            & .MenuSubmenu {
              top: 0;
              right: 100%;
              position: absolute;
              z-index: 24;
              border: 4px solid;
              display: none;
              & .MenuItem {
                z-index: 24;
                border: none;
                & a {
                  border: none;
                }
              }
            }
          }
        }
      }
    }
        
    & .MenuParent {
      &:hover,
      &:active {
        & .MenuSubmenu {
          display: inline-block;

          & .MenuParent {
            display: inline-block;
          
            & .MenuSubmenu {
              display: none;
            }
          }

          & .MenuParent {
            &:hover,
            &:active {
              & .MenuSubmenu {
                display: inline-block;
        
                & .MenuParent {
                  display: inline-block;
                }

                & .MenuSubmenu {
                  display: none;
                }
              }
            }
          }
        }
      }
    }
  }
}
